import vars from '../_vars';

if (vars.$burger) {
  vars.$burger.addEventListener('click', () => {
    vars.$pageBody.classList.add('stop-scroll');
    vars.$right.classList.add('header__right--visible');
    vars.$burger.classList.add('burger--active');
  });

  vars.$navClose.addEventListener('click', () => {
    vars.$pageBody.classList.remove('stop-scroll');
    vars.$right.classList.remove('header__right--visible');
    vars.$burger.classList.remove('burger--active');
  });

  vars.$nav.addEventListener('click', (e) => {
    if (e.target.classList.contains('nav__link')) {
      vars.$right.classList.remove('header__right--visible');
    }
  });
}
